<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="media" ref="mediaForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="media.id"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="media.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="media.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="media" :refId="media.id" v-model="media.fileList" v-if="dialog.open" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "mediaForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        media: {
          id: "",
          name: "",
          remarks: ""
        },
        //
        rules: {
          name: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'media',tableName:'media',column:'name',message:'',trigger:'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getMedia(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getMedia(id) {
        this.$request.doGet("/admin/media/get?id=" + id).then(res => {
          if (res.isOk) {
            this.media = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.mediaForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/media/sub', that.media).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.mediaForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



