<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="bullet" ref="bulletForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="bullet.id"/>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="bullet.userId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="register in registerDict" :key="register.id" :value="register.id" :label="register.loginName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="color">
          <el-input v-model="bullet.color" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="time">
          <el-input v-model="bullet.time" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="bullet.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "bulletForm",
    data() {
      return {
        registerDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        bullet: {
          id: "",
          userId: "",
          color: "",
          time: "",
          content: ""
        },
        //
        rules: {
          userId: [{required: true, message: '', trigger: 'blur'}],
          color: [{required: true, message: '', trigger: 'blur'}],
          time: [{required: true, message: '', trigger: 'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      getRegisterDict() {
        this.$request.doGet('/admin/register/getList').then(res => {
          if (res.isOk) {
            this.registerDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getRegisterDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getBullet(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getBullet(id) {
        this.$request.doGet("/admin/bullet/get?id=" + id).then(res => {
          if (res.isOk) {
            this.bullet = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.bulletForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/bullet/sub', that.bullet).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.bulletForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



